@import url("https://fonts.googleapis.com/css?family=Amatic+SC");

section {
  width: 100vw;
  min-height: 600px;
  height: auto;
}

section .project-text {
  text-align: center;
  margin-bottom: 50px;
}

#projects {
  margin-top: -100px;
  padding-bottom: 100px;
  display: block;
}

/*--------------Project Flex Gallery------------------------*/
.panels-container {
  overflow: hidden;
  min-height: 600px;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.panel {
  background: #6b0f9c;
  -webkit-box-shadow: inset 0 0 0 5px rgba(255, 255, 255, 0.1);
          box-shadow: inset 0 0 0 5px rgba(255, 255, 255, 0.1);
  color: var(--black);
  text-align: center;
  /* Safari transitionend event.propertyName === flex */
  /* Chrome + FF transitionend event.propertyName === flex-grow */
  -webkit-transition: font-size 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11),
    flex 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), background 0.2s;
  -o-transition: font-size 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11),
    flex 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), background 0.2s;
  transition: font-size 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11),
    flex 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), background 0.2s;
  transition: font-size 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11),
    flex 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), background 0.2s,
    -ms-flex 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11);
  font-size: 0.5em;
  font-weight: 600;
  background-size: cover;
  background-position: center;
  -ms-flex: 1 1;
      flex: 1 1;
  min-width: 150px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.panel1 {
  background-image: url(../../img/img-smart-brain.png);
}
.panel2 {
  background-image: url(../../img/img-background-gen.png);
}
.panel3 {
  background-image: url(../../img/img-todo.png);
}
.panel4 {
  background-image: url(../../img/img-pomo-clock.png);
  color: white;
}
.panel5 {
  background-image: url(../../img/img-react-coin.png);
  color: white;
}
.panel6 {
  background-image: url(../../img/img-wiki-search.png);
  color: white;
}

.panel > * {
  margin: 0;
  width: 100%;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  -o-transition: transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.panel > *:first-child {
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
  font-size: 0;
}
.panel.open-active > *:first-child {
  -webkit-transform: translateY(0%);
      -ms-transform: translateY(0%);
          transform: translateY(0%);
  font-size: 2em;
}
.panel > *:last-child {
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
  font-size: 0;
}
.panel.open-active > *:last-child {
  -webkit-transform: translateY(0%);
      -ms-transform: translateY(0%);
          transform: translateY(0%);
  font-size: 2em;
}

.panel p {
  text-transform: uppercase;
  font-family: "Amatic SC", cursive;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.72), 0 0 14px rgba(0, 0, 0, 0.45);
  cursor: pointer;
  line-height: 50px;
  overflow: hidden;
}

.panel a {
  color: white;
  font-size: 60px;
}

.panel p:nth-child(2) {
  font-size: 6em;
}

.panel.open {
  -ms-flex: 5 1;
      flex: 5 1;
  font-size: 20px;
}

.panel.open p {
  line-height: 150px;
}

/* --------------------------------------------------------------------- */
@media only screen and (max-width: 1024px) {
  .panels-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 3500px;
  }

  .panel {
    min-height: 500px;
  }

  .panel > *:first-child {
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
    font-size: 0;
  }
  .panel.open-active > *:first-child {
    -webkit-transform: translateX(0%);
        -ms-transform: translateX(0%);
            transform: translateX(0%);
    font-size: 2em;
  }
  .panel > *:last-child {
    -webkit-transform: translateX(100%);
        -ms-transform: translateX(100%);
            transform: translateX(100%);
    font-size: 0;
  }
  .panel.open-active > *:last-child {
    -webkit-transform: translateX(0%);
        -ms-transform: translateX(0%);
            transform: translateX(0%);
    font-size: 1em;
  }

  .panel5 {
    background-size: cover;
  }
  .panel6 {
    background-size: cover;
  }
}



