header {
  height: 700px;
  min-width: 100vw;
  display: grid;
  justify-items: center;
  background: #0cebeb; /* fallback for old browsers */
  background: -webkit-linear-gradient(left, #29ffc6, #20e3b2, #0cebeb);
  background: -o-linear-gradient(left, #29ffc6, #20e3b2, #0cebeb);
  background: -webkit-gradient(linear, left top, right top, from(#29ffc6), color-stop(#20e3b2), to(#0cebeb));
  background: linear-gradient(to right, #29ffc6, #20e3b2, #0cebeb);
}

header > .banner-text {
  text-align: center;
  height: 400px;
  width: 80vw;
  display: grid;
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
  margin-top: 125px;
}

#home--link {
  top: 0;
}

/* --------------------------------------------------------------------- */
@media only screen and (max-width: 1024px) {
  /* header styles
 ------------------------------------------------------------------ */
  header h1 {
    font-size: 4.5em;
  }
}

@media only screen and (max-width: 830px) {
  /* header styles
 ------------------------------------------------------------------ */
  header h1 {
    font-size: 4em;
  }

  header h3 {
    font-size: 1.1em;
  }
}

@media only screen and (max-width: 500px) {
  /* header styles
 ------------------------------------------------------------------ */
  header h1 {
    font-size: 3.2em;
  }

  header h3 {
    font-size: 1em;
  }
}



